/* Font */
@font-face {
  font-family: EDGE;
  src: url(fonts/EDGE-Medium.woff);
}

@font-face {
  font-family: EDGETHIN;
  src: url(fonts/EDGE-Thin.woff);
}

@font-face {
  font-family: EDGEBOOK;
  src: url(fonts/EDGE-Book.woff);
}
.App {
  font-family: EDGE;
  text-align: center;
  max-width: 360px;
  margin: 0 auto;
  padding: 4px;
}

.header {
  margin-top: 48px;
  height: 48px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  color: #ff7d70;
}

.intro {
  text-align: end;
}

.cta {
  margin-top: 57px;
}

.subtitle {
  margin-top: 24px;
  margin-bottom: 24px;
  font-family: EDGETHIN;
  font-size: 14px;
  text-align: start;
}

.profile-text{
  text-align: initial;
  margin-bottom: 14px;
}

.subject {
  font-family: EDGEBOOK;
  font-size: 14px;
}

.btn {
  min-width: 110px;
  height: 44px;
  border-radius: 8px;
  color: #ff7d70;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #ff7d70;
  margin-bottom: 24px;
  cursor: pointer;
  padding-left: 10px;
  padding-right: 10px;
}

.btn-active {
  background-color: #ff7d70;
  border: none;
  color: white;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
}

.small-btn {
  width: 79px;
  height: 34px;
  background: #ffffff;
  border: 1px solid #ff7d70;
  box-sizing: border-box;
  border-radius: 8px;
  color: #ff7d70;
  margin-left: 14px;
}

.btn-disabled {
  color: #cccccc;
  border: 1px solid #cccccc;
}

.option-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.text {
  left: 0px;
  align-content: flex-start;
  width: 100%;
  text-align: left;
}

.float-right {
  float: right;
}

.btn-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

/* Range Slider */
.form-range {
  width: 100%;
  height: 10px;
  padding: 0;
  background: #cccccc;
  border-radius: 8px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.form-range::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  background: #ff7d70;
  width: 34px;
  height: 27px;
  border-radius: 8px;
  box-shadow: 8px 8px 25px rgba(0, 0, 0, 0.25);
}

/* grid */
.row {
  display: flex;
  flex-wrap: wrap;
  margin-top: calc(var(--bs-gutter-y) * -1);
  margin-right: calc(var(--bs-gutter-x) * -0.5);
  margin-left: calc(var(--bs-gutter-x) * -0.5);
}

.col-100 {
  flex: 0 0 auto;
  width: 100%;
}

.col-80 {
  flex: 0 0 auto;
  width: 80%;
}

.col-75 {
  flex: 0 0 auto;
  width: 75%;
}

.col-60 {
  flex: 0 0 auto;
  width: 60%;
}

.col-50 {
  flex: 0 0 auto;
  width: 50%;
}

.col-33 {
  flex: 0 0 auto;
  width: 33%;
}

.col-25 {
  flex: 0 0 auto;
  width: 25%;
}

.col-20 {
  flex: 0 0 auto;
  width: 20%;
}

.col-10 {
  flex: 0 0 auto;
  width: 10%;
}

.pr-20 {
  padding-right: 20px;
}


.mt-50{
  margin-top:50px;
}

.mt-10{
  margin-top:10px;
}

.small-text{
  font-family:EDGETHIN;
  font-size: 14px;
}

