/* Text (Area)field */
.input-group {
  position: relative;
  display: table;
  border-collapse: separate;
  box-sizing: border-box;
  margin-top: 1rem;
}

.form-control {
  width: 225px;
  height: 34px;
  background: #ffffff;
  border: 1px solid #cccccc;
  box-sizing: border-box;
  border-radius: 8px;
  padding: 10px;
}

.form-control-area {
  width: 320px;
  height: 159px;
  background: #ffffff;
  border: 1px solid #cccccc;
  border-radius: 8px;
  padding: 10px;
}

/* Checkbox */
.checkbox-input {
  display: block;
  position: relative;
  padding-left: 45px;
  margin-bottom: 15px;
  padding-top: 5px;
  cursor: pointer;
  font-size: 14px;
  font-family: EDGEBOOK;
}

input[type="checkbox"] {
  visibility: hidden;
}

.checkbox-text {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  border: 1px solid #ff7d70;
  border-radius: 8px;
}

.checkbox-input input:checked ~ .checkbox-text {
  background-color: #ff7d70;
  border-radius: 8px;
}

/* list */

.list-container {
  width: 100%;
  display: flex;
  flex-direction: row;
}

.list-summary {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-top: 15px;
}

.list-summary-item {
  display: flex;
  max-width: 40%;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 15px;
}

.select-btn {
  display: flex;
  flex: 1;
  align-items: flex-end;
  justify-content: flex-end;
}

.division {
  height: 1px;
  background-color: #cccccc;
}

.value {
  margin-left: 10px;
  font-size: 14px;
}